*{
    box-sizing: border-box;
    padding: 0;
    margin: 0;
}

body{
   overflow-x: hidden;
}

.header{
    width: 100%;
    line-height: 100px;
    background-color: darkgoldenrod;
    display: flex;
    justify-content: start;
    align-items: center;
    color: white;
    padding-left: 50px;
    box-shadow: 5px 3px 15px -5px black;
}

.main_note{
    width: 500px;
    padding: 15px 10px 5px 10px;
    margin: 20px auto;
}

.my_note h1{
    color: #34495e;
}
.my_note p{
    color: #34495e;
}

form{
    min-height: 50px;
    color: white;
    padding: 15px 10px 5px 10px;
    box-shadow: 5px 3px 15px -5px black;
    position: relative;  
}

input{
    width: 100%;
    line-height: 30px;
    background-color: transparent;
    border: none;
    outline: none;
    font-size: 1.5rem;
    margin: 10px 0;
    font-weight: bold;

}

textarea{
    width: 100%;
    line-height: 30px;
    background-color: transparent;
    border: none;
    outline: none;
    font-size: 1rem;
    margin: 10px 0;
}
.btn{
    width: 30px;
    height: 50px;
    position: absolute;
    bottom: -28px;
    left: 400px;
    cursor: pointer;
    background-color: white !important;
}

.btn .plus_sign{
    font-size: 45px !important;
    font-weight: 900;
    color: #f4b400;
    cursor: pointer;
    z-index: 222;
    background: transparent;
}
.btn:hover{
    background: #f4b400 !important;
}

.btn .plus_sign:hover{
    color: #fff;
    background: transparent;
}

.note{
    background: #fff;
    border-radius: 7px;
    box-shadow: 0 2px 5px  #ccc;
    padding: 10px;
    width: 240px;
    margin: 16px;
    float: left;
}

.note h1{
    font-size: 1.1rem;
    margin-bottom: 6px;

}

.note p{
    font-size: 1.1rem;
    margin-bottom: 10px;
    white-space: pre-wrap;
    word-wrap: break-word;
}

.note .btn2{
    position: relative;
    float: right;
    margin-right: 10px;
    color: #f4b400;
    border: none;
    background: transparent !important;
    cursor: pointer;
    outline: none;
    box-shadow: 3px 3px 15px -10px black;
    border-radius: 50%;
}

.deleteIcon{
    width: 0.7em;
    height: 0.7em;

}

.note .btn2:hover{
   background: #f4b400 !important;
}
.deleteIcon:hover{
   color: #fff;
}


footer{
    width: 100%;
    position: absolute;
    bottom: 0;
    left: 50%;
}